import React, { Component, Fragment } from 'react';
import { connect } from 'react-redux';
import Navbar from './Navbar';
import { performLogin, setUserToken, performLogout, performNameChange, performPassChange, getUserData } from '../actions/userActions';
import PropTypes from 'prop-types';
import { LOGIN_URL, LOGOUT_URL, REGISTER_URL, USER_AUTH_URL, USER_PASS_CHANGE_URL, BASE_ROUTER_URL, USER_URL } from '../listrUrls';
import CSRFToken from './csrftoken';
import Button from '@material-ui/core/Button';
import CssBaseline from '@material-ui/core/CssBaseline';
import Grid from '@material-ui/core/Grid';
import FormControl from '@material-ui/core/FormControl';
import Input from '@material-ui/core/Input';
import InputLabel from '@material-ui/core/InputLabel';
import Paper from '@material-ui/core/Paper';
import { withStyles } from '@material-ui/core/styles';
import TextField from '@material-ui/core/TextField';
import { Redirect, Route } from "react-router-dom";


const styles = theme => ({
  layout: {
    width: 'auto',
    display: 'block', // Fix IE11 issue.
    marginLeft: theme.spacing.unit * 3,
    marginRight: theme.spacing.unit * 3,
    [theme.breakpoints.up(400 + theme.spacing.unit * 3 * 2)]: {
      width: 400,
      marginLeft: 'auto',
      marginRight: 'auto',
    },
  },
  paper: {
    width: 'auto',
    marginTop: theme.spacing.unit * 8,
    display: 'flex',
    flexDirection: 'column',
    padding: `${theme.spacing.unit * 2}px ${theme.spacing.unit * 3}px ${theme.spacing.unit * 3}px`,
  },
  form: {
    width: '100%', // Fix IE11 issue.
    marginTop: theme.spacing.unit,
  },
  submit: {
    marginTop: theme.spacing.unit * 3,
  },
  textField: {
    marginLeft: theme.spacing.unit,
    marginRight: theme.spacing.unit,
  },
});

class Account extends Component {

  constructor(props){
    super(props);
    this.state = {
      newUsername: null,
      newUsername2: null,
      newPassword: null,
      newPassword2: null,
      toHome: false,
    }
  }
  componentDidMount(){
    this.checkIfLoggedIn();
  }
  handleChange = (e) => {
    e.preventDefault();
    this.setState({[e.target.id]: e.target.value})
  }

  handleNameChange = (e) => {
    e.preventDefault();
    if(this.state.newUsername == this.state.newUsername2){
      this.props.performNameChange(USER_AUTH_URL, {"username": this.state.newUsername}, this.props.userToken);
    }
  }

  handlePassChange = (e) => {
    e.preventDefault();
    if(this.state.newPassword == this.state.newPassword2){
      this.props.performPassChange(USER_PASS_CHANGE_URL, {"new_password1": this.state.newPassword, "new_password2": this.state.newPassword2}, this.props.userToken)
    }
  }

  checkIfLoggedIn(){
    var cachedToken = localStorage.getItem("token");
    if(cachedToken !== null && cachedToken !== undefined){
      this.props.setUserToken(cachedToken);
      this.props.getUserData(USER_URL, cachedToken);
      return true;
    }
    return false;
  }


  render(){

    const { classes } = this.props;
    if(this.state.toHome){
      this.setState({toHome: false});
      return <Redirect to={BASE_ROUTER_URL} />
    }
    return(
      <Fragment>
        <Navbar />
        <CssBaseline />
        <Grid item xs={12} style={{padding: 40, marginTop: 10}}>
          <Grid container spacing={8} alignItems="center" justify="center">
            <Grid item xs={12} style={{marginBottom: 20}}>
              <header className="App-header" style={{display: "inline-flex"}}>
                <h1 className="App-title">My Account: {this.props.username}</h1>
              </header>
            </Grid>
            <Grid item xs={12} md={4} lg={4}>
              <Paper className={classes.paper}>
                  <header className="App-header">
                    <h3 className="App-title">Change Username</h3>
                  </header>
                  <Grid container spacing={8} alignItems="center" justify="center">
                    <form onSubmit={(e) => this.handleNameChange(e)}>
                      <TextField
                        name="newUsername"
                        type="text"
                        label="Username"
                        id="newUsername"
                        autoComplete='off'
                        onChange={(e) => this.handleChange(e)}
                        variant='outlined'
                        className={classes.textField}
                        required
                      />
                      <TextField
                        name="newUsername2"
                        type="text"
                        label="Confirm Username"
                        id="newUsername2"
                        autoComplete='off'
                        onChange={(e) => this.handleChange(e)}
                        variant='outlined'
                        className={classes.textField}
                        required
                      />
                      <Button
                        type="submit"
                        variant="raised"
                        color="primary"
                        style={{marginLeft: 5, marginTop:10}}
                        >
                        Change Username
                      </Button>
                    </form>
                  </Grid>
              </Paper>
            </Grid>
            <Grid item xs={12} md={4} lg={4}>
              <Paper className={classes.paper}>
                  <header className="App-header">
                    <h3 className="App-title">Change Password</h3>
                  </header>
                  <Grid container spacing={8} alignItems="center" justify="center">
                  <form onSubmit={(e) => this.handlePassChange(e)}>
                    <TextField
                      name="newPassword"
                      type="password"
                      label="New Password"
                      id="newPassword"
                      autoComplete='off'
                      onChange={(e) => this.handleChange(e)}
                      variant='outlined'
                      className={classes.textField}
                      required
                    />
                    <TextField
                      name="newPassword2"
                      type="password"
                      label="Confirm New Password"
                      id="newPassword2"
                      autoComplete='off'
                      onChange={(e) => this.handleChange(e)}
                      variant='outlined'
                      className={classes.textField}
                      required
                    />
                    <Button
                      type="submit"
                      variant="raised"
                      color="primary"
                      style={{marginLeft: 5, marginTop:10}}
                      >
                      Change Password
                    </Button>
                  </form>
                </Grid>
              </Paper>
            </Grid>
          </Grid>
        </Grid>
      </Fragment>
    );
  }

}

Account.proptypes = {

}

const mapStateToProps = state => ({
  userToken: state.users.token,
  username: state.users.username
});

const ToExport = withStyles(styles)(Account);
export default connect(mapStateToProps, { performLogin, setUserToken, performLogout, performNameChange, performPassChange, getUserData })(ToExport);



